<template>
    <div class="cv-grid">
        <!-- table -->
        <el-table :data="data" v-loading="loading">
            <slot></slot>
        </el-table>

        <!--  -->
        <el-pagination :total="total" small 
        v-show="total == 0 ? false : true"
        class="cv-grid-pagination"
        layout="total, sizes, prev, pager, next" 
        background
        @current-change="handlerCurrentChange"
        @size-change="handlerSizeChange"
        :page-sizes="[10,20,30,40,50]" />
    </div>
</template>

<script>
import service from 'utils/service'

export default {
    name: 'CvGrid',
    props: {
        // api
        url: String,
        value: {},
        // 
        isInitial: {
            type: Boolean,
            default: true
        }
    },
    data() {
        return {
            data: [],
            total: 0,
            loading: false,
            current: 1,
            size: 10
        }
    },
    mounted() {
        this.isInitial && this.tableInitial();
    },
    methods: {
        /**
         * 
         * @param {Boolean} repeat true:size10current1
         */
        async tableInitial(repeat=true) {
            const params = {
                current: repeat ? 1 : this.current,
                size: repeat ? 10 : this.size,
                ...this.value
            };

            try {
                this.loading = true;
                const { data, flag } = await this.$http({
                    url: this.url,
                    params
                });
                this.loading = false;

                if(flag && service.isObject(data) && Array.isArray(data.records)) {
                    this.data = data.records;
                    this.total = data.total;
                }else {
                    this.data = [];
                    this.total = 0;
                }
            }
            catch(e) {
                this.loading = false;
            }
        },

        /**
         * currentPage 
         * @param {Number} page: currentPage
         */
        handlerCurrentChange(page) {
            this.current = page;
            this.tableInitial(false);
        },  

        /**
         * pageSize 
         * @param {Number} size: size
         */
        handlerSizeChange(size) {
            this.size = size;
            this.tableInitial(false);
        }
    }
}
</script>

<style lang="scss">
    .cv-grid {
        .cv-grid-pagination {
            margin-top: 5px;
            text-align: right;
        }
    }
</style>





