<template>
    <div class="cv-dropdown-tree" v-clickoutside="handlerIstoggle">
        <el-input readonly clearable @click.native="isToggle=!isToggle" v-model="treeContent" />

        <!--  -->
        <transition name="el-zoom-in-top">
			<div class="dropdown-treearea" v-show="!readonly && isToggle">
				<el-input placeholder="" class="dropdown-search" v-model="search" clearable />
				<el-scrollbar 
                    tag="ul"
                    wrap-class="el-select-dropdown__wrap"
                    view-class="el-select-dropdown__list" v-loading="treeLoading">
                    <cv-tree 
                    ref="tree"
                    :data="data ? data : treeData" 
                    :is-check="isCheck" 
                    :tree-loaded="handlerTreeLoaded"
                    @node-click="handlerNodeClick" />
                </el-scrollbar>
			</div>
		</transition>
    </div>
</template>

<script>
import CvTree from 'components/tree'
import service from 'utils/service'

/**
 * 
 * 
 */
export default {
    name: 'CvDropdownTree',
    props: {
        // (id)
        value: {},
        // 
        label: {},
        // 
        readonly: {
            type: Boolean,
            default: false
        },
        // 
        url: String,
        // 
        data: Array,
        // 
        autoParams: {
            type: Object,
            default() {return {} }
        },
        // ()
        isInitial: {
            type: Boolean,
            default: true
        },
        // 
        isCheck: Boolean
    },
    watch: {
        value: {
            immediate: true,
            handler(newVal) {
                if(newVal && this.$refs['tree']) {     
                    this.accordValueFindNode(newVal);
                }
            }
        },
        // 
        autoParams(val) {
            // 
            this.treeContent = '';

            // key-valueundefined
            if(this.eachAutoParamsIsExistValue(this.autoParams)) {
                // 
               this.treeContent = '';
               // 
               this.treeData = [];
            }else {
                 this.treeRequestType();
            }  
        },
        // 
        treeContent(val) {
            val == '' && this.accordValueFindNode(val);
        }
    },
    data() {
        return {
            treeData: [],
            treeContent: '',
            search: "",
            isToggle: false,
            treeLoading: false
        }
    },
    mounted() {
        //  
        if(!this.data && this.isInitial) {
            this.treeRequestType();
        }
    },
    methods: {
        /**
         * 
         */
        handlerIstoggle() {
            this.isToggle = false;
        },

        /**
         * 
         * @param {Object} node: 
         */
        handlerNodeClick(node) {
            this.isToggle = false;
            this.accordValueFindNode(node.id);
        },

        /**
         * 
         * @param {Array} data: 
         */
        handlerTreeLoaded(data) {
            if(this.value && this.$refs['tree']) {
                 this.accordValueFindNode(this.value);
            }
        },
        
        /**
         * v-model(id)
         * @param {String} key: id
         */
        accordValueFindNode(key) {
            const _treeMethods = this.$refs['tree'];
            const nodes = _treeMethods.getNodeByParam('id', key);

            if(nodes && typeof nodes === 'object') {
                // textnode
                this.$emit('update:label', nodes);
                this.$emit('input', nodes.id);
                _treeMethods.selectNode(nodes);
                this.treeContent = nodes.text;
            }else {
                //  (null)
                this.$emit('update:label', {});
                this.$emit('input', null);
                _treeMethods.cancelSelectedNode();
            }
        },

        /**
         * http
         */
        async treeRequestType() {
            this.treeLoading = true;
            const {data, flag} = await this.$http({
                url: this.url,
                params: this.autoParams
            });
            this.treeLoading = false;

            if(flag && data) {
                this.treeData = data;
            }else {
                this.treeData = [];
            }
        },

        /**
         * autoParams key value
         * @param {Object} params: {}
         * @return {Boolean} true: key-value
         */
        eachAutoParamsIsExistValue(params) {
            var keys = Object.keys(params);
            
            if(keys.length === 0) return false;

            for(let i = 0, len=keys.length; i<len; i++) {
                if(params[keys[i]] === undefined) {
                     return true;
                 }
            }
            return false;
        }
    }
}
</script>

<style lang="scss">
    @import '../../../theme/color/index.scss';
    .cv-dropdown-tree {
        position: relative;
        display: inline-block;
        .dropdown-treearea {
            position: absolute;
            top: 100%;
            z-index: 1;
			margin-top: 5px;
			border-radius: 2px;
			width: 100%;
			max-height: 336px;
			background: #fff;
			border: 1px solid $lightGray;
			box-shadow: $box-shadow;
        }
        .dropdown-search {
            width: 92%;
            margin: 5px 4%;
        }
    }
</style>





